@import url("https://fonts.googleapis.com/css?family=Montserrat:300,400,700,800");
* {
  box-sizing: border-box;
  margin: 0;
  /* display: flex;
  flex-grow: initial; */
}

html,
body {
  margin: 0;
  background: lightgray;
  font-family: "Montserrat", helvetica, arial, sans-serif;
  font-size: 14px;
  font-weight: 400;
  /* padding-left: 15px; */
}

.movieTitle {
  width: 100%;
  background-color: rgb(46, 13, 13);
  height: 23px;
  border-radius: 2px;
  font-weight: 400;
  font-size: 15px;
  padding: 3px;
  overflow: hidden;
  /* text-align: center; */
}

.Images {
  /* padding-top: 7px; */
  height: 250px;
  width: 180px;
  padding: auto;
  align-content: center;
  border-radius: 10px;
}

.link {
  display: block;
  text-align: center;
  color: #777;
  text-decoration: none;
  padding: 10px;
}
